// navbar
.navbar{
    position: fixed;
    left: 0;
    right:0;
    top:0;
    background-color: rgba(0,0,0,.6);
}
.dropdown{
    margin: 0 80px 0 0 ;
}

// header
#header{
    background-image: url(images/headerPic.jpg);
    background-size: cover;
    h1{
        font-size: 50px;
        font-weight: bold;
        color:#4C0DE8;
    }
    h2{
        font-size: 25px;
        font-weight: bold;
    }
    .dropdown{
        width: 100%;
    }
    button{
        width: 100%;
    }
    .dropdown-menu{
        width: 100%;
    }
}

// map
#map {
    margin: 20px auto 0; 
    height: 400px;
    width: 80%;
  }

// content
.alter-number{
    line-height: 50px;
    font-weight: bold;
    padding-left: 50px;
    color:#1C5282;
}

// footer
.footer{
    height: 30px;
}
